import { PrimaryButton } from '../../UI/buttons/PrimaryButton'
import { SecondaryButton } from '../../UI/buttons/SecondaryButton'
import { BaseModal } from '../../UI/modals/BaseModal'
import { _decorator, EditBox, Input, Label, Node, RichText } from 'cc'
const { ccclass, property } = _decorator

@ccclass('ModalInputPassword')
export class ModalInputPassword extends BaseModal {
	@property(PrimaryButton)
	confirmButton: PrimaryButton | null = null

	@property(SecondaryButton)
	cancelButton: SecondaryButton | null = null

	@property(EditBox)
	passwordEditbox: EditBox | null = null

	@property(Label)
	inputLabel: Label | null = null

	@property(RichText)
	message: RichText | null = null

	constructor() {
		super({ showCloseButton: false, showOverlay: true })
	}

	start() {
		super.start()
		this.confirmButton?.node?.on(Node.EventType.TOUCH_END, this.onClickConfirmBtn, this)
		this.node.on('open', () => {
			this.message && (this.message.string = '')
			this.confirmButton.interactable = false
			this.passwordEditbox?.focus()
		})

		this.cancelButton?.node?.on(Node.EventType.TOUCH_END, this.onClickCancelBtn, this)
		this.passwordEditbox?.node?.on(EditBox.EventType.TEXT_CHANGED, this.onInputPassword, this)
	}

	update(deltaTime: number) {}

	protected onClickConfirmBtn() {
		this.node.emit('confirm', this.passwordEditbox?.string || '')
	}

	protected onClickCancelBtn() {
		this.node.emit('cancel')
	}

	protected onInputPassword() {
		if (this.passwordEditbox?.string && this.passwordEditbox.string.length > 0) {
			this.confirmButton.interactable = true
		} else {
			this.confirmButton.interactable = false
		}
	}

	public setMessage(message: string) {
		this.message && (this.message.string = message)
	}
}
