import {
	_decorator,
	Animation,
	assetManager,
	Button,
	Component,
	director,
	Label,
	Node,
	ProgressBar,
	resources,
	sys,
	Toggle,
} from 'cc'
import { add } from 'lodash-es'
import { AudioControl } from './AudioControl'
import { PrimaryButton } from '../UI/buttons/PrimaryButton'
import { GameInteraction } from '../common/web-interaction'
import { BaseModal } from '../UI/modals/BaseModal'
import { RequestFactory } from '../common/request-factory'
import { gameApi } from '../common/game-apis'
import configs from '../common/constants/config'
import { socketClient } from '../common/constants/socket-client'
const { ccclass, property } = _decorator

@ccclass('StartScene')
export class StartScene extends Component {
	@property(PrimaryButton)
	startGameBtn: PrimaryButton | null = null

	@property({
		type: AudioControl,
		tooltip: 'add audio controller',
	})
	public clip: AudioControl

	@property({
		type: ProgressBar,
		tooltip: 'loading progress bar',
	})
	loadingBar: ProgressBar | null = null

	@property({
		type: Label,
		tooltip: 'loading bar label',
	})
	loadingLabel: Label | null = null

	@property({
		type: Toggle,
		tooltip: 'agree term toggle',
	})
	agreeTermToggle: Toggle | null = null

	@property({
		type: BaseModal,
		tooltip: 'add modal info',
	})
	modalInfo: BaseModal | null = null

	@property(Button)
	retryBtn: Button | null = null

	isAuthenticated: boolean = false

	start() {
		this.startGameBtn?.node.on(Node.EventType.TOUCH_END, this.initGame, this)
		this.agreeTermToggle?.node?.on(Node.EventType.TOUCH_END, this.onClickAgreeTerm, this)
		new GameInteraction()
	}

	update(deltaTime: number) {
		// console.log('Update', deltaTime)
		if (this.startGameBtn) {
			this.startGameBtn.interactable = this.isEnableStartBtn
		}
	}

	get isEnableStartBtn() {
		return (
			this.agreeTermToggle?.isChecked && this.loadingBar?.progress === 1 && this.isAuthenticated
		)
	}

	protected onClickAgreeTerm(): void {
		console.log('Click agree term', this.agreeTermToggle?.isChecked)
	}

	protected onLoad(): void {
		console.log('OnLoad', add(1, 2)) // test lodash
		this.clip.onAudioLoop(0) // background music
	}
	protected onEnable(): void {
		this.preloadAllAssets()

		RequestFactory.requestAuthorization()
			.then((res) => {
				console.log('fetchAuthorization', res)
				sys.localStorage.setItem('accessToken', res.accessToken)
				socketClient.setAuth(res.accessToken)
				socketClient.connect()
				return RequestFactory.fetchUserInfo()
			})
			.then((rs) => {
				console.log('fetchUserInfo', rs)
				if (rs?.data) {
					sys.localStorage.setItem('user-info', JSON.stringify(rs.data))
					this.isAuthenticated = true
				} else {
					this.isAuthenticated = false
					console.log('fetchUserInfo error', rs)
					throw new Error('fetchUserInfo error')
				}
			})
			.catch((err) => {
				console.log('fetchAuthorization', err)
				sys.localStorage.removeItem('accessToken')
				sys.localStorage.removeItem('user-info')
				this.isAuthenticated = false

				console.log('this.modalInfo', this.modalInfo)
				this.modalInfo?.show()
				this.retryBtn?.node.on(Node.EventType.TOUCH_END, () => {
					// reload page
					window.location.reload()
				})
			})

		// Mock response data
		// TODO: Remove this
		setTimeout(() => {
			gameApi.response({
				protocol: 'game-api',
				type: 'response',
				payload: {
					fncName: 'fetchAuthorization',
					args: [],
					result: JSON.stringify({
						accessToken:
							'eyJhbGciOiJIUzI1NiIsInR5cCI6IkpXVCJ9.eyJ3YWxsZXRBZGRyZXNzIjoiYWRkcl90ZXN0MXFyc3g3MmhydjhlbnM5MGh3a2V6Zzd5c3lod3Zjam15emR2ZXlmODhwcHE3YTBsd3U3Z3Ywd3V1ZjlsaHptN3djbHZqNW50Z2NmYTUzajBycXhtdTIzN3gyMHhzbmU1NnEzIiwiaWQiOjEsImFsaWFzIjoiSm9obkRvZSIsInJvbGVzIjpbInVzZXIiLCJhZG1pbiJdLCJpYXQiOjE3NDkwMjY0OTQsImV4cCI6MTc0OTg5MDQ5NH0.KqRAJx_BZztXPiM5w_SV_r52S18RtcAYgmlD2UiHaFo',
					}),
					error: null,
				},
			})
		}, 1000)
	}

	protected initGame(): void {
		if (!this.agreeTermToggle?.isChecked) {
			console.log('Please agree to the term')
			return
		}
		if (this.loadingBar.progress !== 1) {
			return
		}
		director.loadScene('game-lobby')
	}

	private preloadAllAssets() {
		// Ti ton b assets trong th mc resources
		this.loadingBar.progress = 0
		this.loadingLabel.string = 'Loading... 0%'
		resources.loadDir(
			'',
			(loadedAssets, totalAssets, assets) => {
				// Cp nht tin trnh ti
				const progress = loadedAssets / totalAssets
				this.loadingBar.progress = progress
				this.loadingLabel.string = `Loading... ${Math.floor(progress * 100)}%`
			},
			(error, assets) => {
				if (error) {
					console.error('Failed to preload assets:', error)
					return
				}
				this.loadingLabel.string = 'Click start now to play'
				this.loadingLabel.getComponent(Animation)?.play()
				this.loadingBar.progress = 1
			}
		)
	}
}
