import { sys } from 'cc'
import { Room } from '../types/room.type'

/**
 * Game APIs
 * from WEB to GAME:
 * - initGame
 * - createLobby
 * - joinLobby
 * - setLobbyItems
 * - renderLobbyList
 * - setLoading
 * - setAvatar
 * - setUserInfo
 * - setChoice
 * - setGameState
 * - setRoundResult
 * - setPayout
 * - setNextRound
 * - setWaitingForPlayers
 * - setWaitingForChoices
 * - setCommittingChoices
 * - setRevealingChoices
 * - setWaitingForPayout
 * - setWaitingForNextRound
 *
 *
 * from GAME to WEB:
 * - onReady
 * - onChoice
 * - onRevealChoice
 * - onConfirm
 */

// Define a global object to store functions

export namespace GameApiNsp {
	export type InteractFunction = (
		...args: unknown[]
	) => void | Promise<void> | unknown | Promise<unknown>

	export type RequestFncName =
		// Player
		| 'fetchAuthorization'
		| 'fetchUserInfo'
		| 'fetchUserBalance'
		| 'depositBalance'
		// Lobby
		| 'fetchLobbyList'
		| 'createLobby'
		| 'joinLobby'
		| 'leaveLobby'
		// Game
		| 'startGame'
		| 'commitChoice'
		| 'revealChoice'
		| 'createPayout'
		| 'ready'
		| 'leaveGame'
		// Blockchain
		| 'buildTransaction'
		| 'signTransaction'
		| 'submitTransaction'

	export type InteractionMessage = {
		protocol: 'game-api'
		type: 'request' | 'response'
		payload: {
			fncName: RequestFncName
			args: any[]
			result?: any
			error?: any
		}
	}

	export class GameApi {
		functions: Record<string, InteractFunction> = {}
		windowInstance: Window
		constructor(window: Window) {
			this.windowInstance = window
			// this.functions = (window as any).GAME_API.functions as any
		}
		setFunc(name: string, fn: InteractFunction) {
			if (typeof fn === 'function') {
				this.functions[name] = fn
				return this
			} else {
				throw new Error(`Provided value for "${name}" is not a function`)
			}
		}

		call<T = InteractFunction>(name: string, ...args: any[]) {
			if (typeof this.functions[name] === 'function') {
				console.log('[GameApi][call]: ', `Function [${name}] `, `Args: `, args)
				return this.functions[name](...args) as T
			} else {
				throw new Error(`Function "${name}" does not exist`)
			}
		}

		request<T>(
			name: RequestFncName,
			{ args = null, timeout = 60000 }: { args?: any; timeout?: number } = {}
		) {
			return new Promise<T>((resolve, reject) => {
				try {
					const data: InteractionMessage = {
						protocol: 'game-api',
						type: 'request',
						payload: {
							fncName: name,
							args,
						},
					}
					this.windowInstance.parent.postMessage(data, '*')
					this.windowInstance.addEventListener(
						'message',
						(event: MessageEvent<InteractionMessage>) => {
							if (event.data.protocol !== 'game-api') return
							console.log('[GameApi][listen-message]: ', event.data)
							const { type, payload } = event.data
							if (type === 'response' && payload.fncName === name) {
								try {
									if (payload.error) {
										reject(new Error(`[GameApi][request]: ${payload.error}`))
										return
									}
									const result = JSON.parse(payload.result)
									resolve(result)
								} catch (error) {
									reject(new Error(`[GameApi][request]: Error parsing response: ${error}`))
								}
							}
						}
						// { once: true}
					)
					setTimeout(() => {
						reject(new Error(`[GameApi][request]: Request "${name}" timed out`))
					}, timeout)
				} catch (error) {
					reject(error)
				}
			})
		}

		response(data: InteractionMessage) {
			console.log('[GameApi][mock-response]: ', data)
			this.windowInstance.postMessage(data, '*')
		}
	}
}

export const gameApi = new GameApiNsp.GameApi(window)
