import { _decorator, Button, Component, director, math, Node, RichText, sys, tween } from 'cc'
import { Room } from '../../common/types/room.type'
import { CurrencyBalance } from '../../common/types/currency.type'
const { ccclass, property } = _decorator

@ccclass('Navigation')
export class Navigation extends Component {
	@property(Button)
	navBackBtn: Button | null = null

	@property(RichText)
	betAmountText: RichText | null = null

	@property(RichText)
	roomCodeText: RichText | null = null

	start() {}

	protected onLoad(): void {
		this.navBackBtn.node.on(Node.EventType.TOUCH_END, this.onClickBackBtn, this)
	}

	update(deltaTime: number) {}

	protected updateRoomData() {
		try {
			const room = JSON.parse(sys.localStorage.getItem('currentRoom') || 'null') as Room
			if (!room) return
			this.betAmountText.string =
				'Bet Amount: ' + new CurrencyBalance(room.betUnit, room.betAmount).toAda(true)
			this.roomCodeText.string = 'Room Code: ' + room.code
		} catch (error) {
			console.log('updateRoomData', error)
		}
	}

	protected onClickBackBtn(): void {
		this.node.emit('back')
	}

	public set interactable(value: boolean) {
		if (!this.navBackBtn) return
		this.navBackBtn.interactable = value

		if (value) {
			this.updateRoomData()
			tween(this.node)
				.to(1, { x: -616, scale: math.v3(1, 1, 1) })
				.start()
		} else {
			tween(this.node)
				.to(1, { x: -616 - 424 - 100, scale: math.v3(0.9, 0.9, 0.9) })
				.start()
		}
	}

	public get interactable() {
		if (!this.navBackBtn) return false
		return this.navBackBtn.interactable
	}
}
