import { _decorator, Button, Color, Component, Label, Node } from 'cc'
import { BaseButton } from './BaseButton'
const { ccclass, property } = _decorator

@ccclass('SecondaryButton')
export class SecondaryButton extends BaseButton {
	constructor() {
		super()
		this.defaultColor = new Color('#652615')
		this.disabledColor = new Color('#999999')
		this.hoveredColor = new Color('#913820')
		this.pressedColor = new Color('#652615')
		this.defaultOutlineColor = new Color('#652615')
		this.disabledOutlineColor = new Color('#999999')
	}
}
