export function formatString(str: string | null, begin = 5, last = 5): string {
	if (!str) return ''
	if (str.length <= begin + last) return str
	const before = str.substring(0, begin)
	const after = str.substring(str.length - last)
	return before + '...' + after
}

export function formatAddress(
	address: string | null,
	options: { begin?: number; last?: number; splitBy?: string } = {
		begin: 5,
		last: 5,
		splitBy: '...',
	}
): string {
	if (!address) return ''
	const { begin = 5, last = 5, splitBy = '...' } = options
	if (address.length <= begin + last) return address
	const before = address.substring(0, begin)
	const after = address.substring(address.length - last)
	return before + splitBy + after
}
