import {
	_decorator,
	Component,
	resources,
	Sprite,
	SpriteFrame,
	Node,
	assetManager,
	Texture2D,
	ImageAsset,
	RichText,
} from 'cc'
import { GameInteraction } from '../common/web-interaction/index'
import { User, UserBalance } from '../common/types/user.type'
import { RequestFactory } from '../common/request-factory'
import { formatAddress, formatString } from '../common/utils/useFormat'
const { ccclass, property } = _decorator

@ccclass('PlayerManager')
export class PlayerManager extends Component {
	@property(Sprite)
	playerAvatar: Sprite | null = null

	@property(RichText)
	username: RichText | null = null
	@property(RichText)
	walletAddress: RichText | null = null
	@property(RichText)
	userCoin: RichText | null = null

	userInfo: User = {
		walletAddress: '',
		avatar: '',
		id: 0,
		alias: '',
		createdAt: '',
	}

	start() {}

	onLoad() {
		this.playerAvatar.node.on(Node.EventType.TOUCH_END, this.onClickAvatar, this)
	}

	update(deltaTime: number) {}

	protected setAvatar(avatarUrl: string): void {
		assetManager.loadRemote(avatarUrl, (err, imageAsset) => {
			if (err) {
				console.error('Li khi ti avatar:', err)
				return
			}
			if (this.playerAvatar) {
				const spriteFrame = new SpriteFrame()
				const texture = new Texture2D()
				texture.image = imageAsset as ImageAsset
				spriteFrame.texture = texture
				this.playerAvatar.spriteFrame = spriteFrame
			}
		})
	}

	protected onClickAvatar(): void {
		console.log('Click avatar')
	}

	public setUserInfo(data: User): void {
		this.userInfo = data

		this.username.string = data.alias
		this.walletAddress.string = formatAddress(data.walletAddress, {
			begin: 6,
			last: 4,
			splitBy: '...',
		})
		this.setAvatar(data.avatar)
	}

	public setUserBalance(data: UserBalance): void {
		this.userCoin.string = `${data.coin} ${data.coinSymbol}`
	}
}
