import { _decorator, Button, Component, Node, RichText, UIOpacity, UITransform } from 'cc'
import { Room } from '../../common/types/room.type'
import { formatString } from '../../common/utils/useFormat'
import { CHAIN } from '../../common/constants/chain'
import { CurrencyBalance } from '../../common/types/currency.type'
const { ccclass, property } = _decorator

@ccclass('LobbyItem')
export class LobbyItem extends Component {
	@property(Node)
	lockedIcon: Node | null = null

	@property(RichText)
	lobbyShowedName: RichText | null = null

	@property(RichText)
	lobbyBetAmountText: RichText | null = null

	@property(Button)
	joinButton: Button | null = null

	// Internal data
	roomData: Room | null = null

	protected onEnable(): void {
		if (!this.joinButton) {
			return
		}
		this.joinButton.node.on(Node.EventType.TOUCH_END, this.onClickJoinButton, this)
	}

	onLoad() {
		if (!this.roomData) {
			return
		}
		if (this.roomData.requiredPassword && this.lockedIcon) {
			this.lockedIcon.active = true
		} else {
			this.lockedIcon.active = false
		}

		// Showed name's max length is 20
		this.lobbyShowedName &&
			(this.lobbyShowedName.string = formatString(this.roomData.customName, 20, 0))
		// Bet amount
		let betAmountText = new CurrencyBalance(this.roomData.betUnit, this.roomData.betAmount).toAda(
			true
		)
		// if (this.roomData.betUnit.symbol === 'lovelace') {
		// 	betAmountText = this.roomData.betAmount / 1000000 + ' ' + CHAIN.symbol
		// } else {
		// 	// TODO: Check if asset is token
		// 	betAmountText = this.roomData.betAmount + ' ' + this.roomData.betUnit.symbol
		// }
		this.lobbyBetAmountText && (this.lobbyBetAmountText.string = betAmountText)
	}

	update(deltaTime: number) {}

	protected onClickJoinButton(): void {
		this.node.emit('join-lobby', this.roomData)
	}
}
