import { sys } from 'cc'
import { gameApi } from './game-apis'
import configs from './constants/config'

export const $fetch = async (url: string, options?: RequestInit) => {
	return new Promise<any>((resolve, reject) => {
		const token = sys.localStorage.getItem('accessToken')
		fetch(url, {
			...options,
			headers: {
				...options?.headers,
				Authorization: token ? `Bearer ${token}` : '',
			},
		})
			.then((response: Response) => {
				return response.json()
			})
			.then((value) => {
				resolve(value)
			})
			.catch((error) => {
				reject(error)
			})
	})
}

export class RequestFactory {
	static baseApiUrl = configs.baseApiUrl

	static getLobbyList() {
		return $fetch(`${this.baseApiUrl}/rooms`)
	}

	static createLobby(body: Record<string, any>) {
		return $fetch(`${this.baseApiUrl}/rooms`, {
			method: 'POST',
			body: JSON.stringify(body),
		})
	}

	// User
	static fetchUserInfo() {
		return $fetch(`${this.baseApiUrl}/auth`)
	}
	static fetchUserBalance() {
		return $fetch(`${this.baseApiUrl}/auth/balance`)
	}
	static depositBalance(body: Record<string, any>) {
		return $fetch(`${this.baseApiUrl}/auth/deposit`, {
			method: 'POST',
			body: JSON.stringify(body),
		})
	}
	static async requestAuthorization() {
		const rs = await (gameApi.request('fetchAuthorization') as Promise<{ accessToken: string }>)
		return rs
	}
}
