import { _decorator, Node } from 'cc'
import { BaseModal } from '../UI/modals/BaseModal'
import { PrimaryButton } from '../UI/buttons/PrimaryButton'
const { ccclass, property } = _decorator

@ccclass('ModalReconnect')
export class ModalReconnect extends BaseModal {
	@property(PrimaryButton)
	reloadButton: PrimaryButton | null = null

	start() {
		super.start()

		this.reloadButton?.node?.on(Node.EventType.TOUCH_END, this.onClickReloadBtn, this)
	}

	update(deltaTime: number) {}

	protected onClickReloadBtn() {
		window.location.reload()
	}
}
