import {
	_decorator,
	Button,
	Component,
	director,
	instantiate,
	Node,
	Prefab,
	sys,
	UIOpacityComponent,
} from 'cc'
const { ccclass, property } = _decorator

import { default as jsSha256 } from 'js-sha256'
import { Choice } from '../common/types/choice.type'
import { PrimaryBtn } from './gameplay/PrimaryBtn'
import { AudioControl } from './AudioControl'
import { ChoiceBtnScript } from './gameplay/ChoiceBtnScript'
import { CountdownController } from './CountdownController'
import { Navigation } from './gameplay/Navigation'
import { RoundResultController } from './gameplay/RoundResultController'
import { Player } from './gameplay/Player'
import { BackgroundRunner } from '../common/utils/backgroundRunner'
import { socketClient } from '../common/constants/socket-client'
import { Room } from '../common/types/room.type'
import { ModalReconnect } from './ModalReconnect'
import { GamePlayer, GameState, SocketGame } from '../common/types/game.type'
import {
	EmitMessageType,
	GameChatResponseMessage,
	GameResponseMessage,
	GameStateChangedResponseMessage,
	RoomActionResponse,
} from '../common/types/socket-client.type'
import { User } from '../common/types/user.type'
import { CurrencyBalance } from '../common/types/currency.type'
import { ChatManager } from './chat/ChatManager'
import { formatString } from '../common/utils/useFormat'

@ccclass('GamePlayManager')
export class GamePlayManager extends Component {
	@property({
		type: PrimaryBtn,
		tooltip: 'add primary btn',
	})
	primaryBtn: PrimaryBtn

	@property({
		type: Navigation,
		tooltip: 'add navigation',
	})
	nav: Navigation

	@property(Node)
	uiController: Node | null = null

	@property(ModalReconnect)
	modalReconnect: ModalReconnect | null = null

	@property({
		type: ChatManager,
		tooltip: 'add chat manager',
	})
	chatManager: ChatManager | null = null

	@property({
		type: AudioControl,
		tooltip: 'add audio controller',
	})
	public clip: AudioControl

	@property(Node)
	rockBtnNode: Node | null = null
	@property(Node)
	paperBtnNode: Node | null = null
	@property(Node)
	scissorsBtnNode: Node | null = null

	@property({
		type: CountdownController,
		tooltip: 'add countdown controller',
	})
	countdownController: CountdownController
	@property({
		type: RoundResultController,
		tooltip: 'add round result controller',
	})
	roundResultController: RoundResultController | null = null

	//
	@property({
		type: Player,
		tooltip: 'add player A',
	})
	playerA: Player
	@property({
		type: Player,
		tooltip: 'add player B',
	})
	playerB: Player

	private _gameState: GameState
	choiceMapper: Record<Choice, Node> = {
		[Choice.None]: null,
		[Choice.Rock]: this.rockBtnNode,
		[Choice.Paper]: this.paperBtnNode,
		[Choice.Scissors]: this.scissorsBtnNode,
	}
	socketRoom = ''
	currentRoom: Room | null = null
	currentGame: SocketGame | null = null
	selectedChoice: Choice = Choice.Rock

	protected start() {
		console.log('[GamePlayManager][start]')
	}
	protected onLoad() {
		BackgroundRunner.enableBackgroundRunning((dt: number) => {
			// Cp nht logic game, v d: timer hoc trng thi
			director.getScheduler().update(dt)
		})
	}

	protected onEnable() {
		console.log('[GamePlayManager][onEnable]')
		// Load data from local storage
		this.socketRoom = sys.localStorage.getItem('socket-room') || ''
		if (!this.socketRoom) {
			console.error('Not found socket room in localstorage')
			return
		}
		this.currentRoom = JSON.parse(sys.localStorage.getItem('currentRoom') || 'null') as Room | null
		if (!this.currentRoom) {
			console.error('Not found currentRomm in localstorage')
			return
		}
		this.currentGame = JSON.parse(
			sys.localStorage.getItem('currentGame') || 'null'
		) as SocketGame | null
		if (!this.currentGame) {
			console.error('Not found currentGame in localstorage')
			return
		}
		console.log(
			'[GamePlayManager][localStorage]',
			this.socketRoom,
			this.currentRoom,
			this.currentGame
		)
		// init event listeners
		this.primaryBtn.node.on(
			Node.EventType.TOUCH_END,
			() => {
				this.onClickPrimaryBtn(this.primaryBtn)
			},
			this
		)
		this.initChoiceBtn()
		this.roundResultController?.node.on('replay', () => {
			// NOTE: Do not use socket event
			this.gameState = GameState.WaitingForPlayers
		})
		this.nav.node.on('back', () => {
			director.loadScene('game-lobby')

			socketClient.emit('ROOM_ACTION', {
				roomId: this.currentRoom.id,
				action: 'LEAVE',
				socketRoom: this.socketRoom || '',
			})
		})

		// init game state
		this.gameState = GameState.WaitingForNextRound
		this.pushSystemNoti(`Welcome to Rock Paper Scissors!`)
		this.chatManager.node.on('send-message', (message: string) => {
			socketClient.emit('GAME_CHAT', {
				socketRoom: this.socketRoom,
				gameRoomId: this.currentRoom.id,
				message,
			})
		})
		// init player data
		this.initPlayerData()
		// init socket event
		this.updatePlayerData(this.currentGame?.players || [])

		socketClient.listen('MESSAGE', (message) => {
			console.log('MESSAGE', message)
			if (message === 'ANOTHER_SESSION_LOGIN') {
				this.modalReconnect?.show()
			}
		})
		socketClient.listen('DISCONNECT', (payload) => {
			console.log('ROOM_ACTION', payload)
			this.modalReconnect?.show()
		})

		socketClient.listen('ROOM_ACTION', (payload) => {
			this.handleSocketEventRoomAction(payload)
		})
		socketClient.listen('GAME', (payload) => {
			this.handleSocketEventGame(payload)
		})
		socketClient.listen('GAME_STATE_CHANGED', (payload) => {
			this.handleSocketEventGameStateChange(payload)
		})
		socketClient.listen('GAME_CHAT', (payload) => {
			this.handleSocketEventGameChat(payload)
		})
	}

	protected update(deltaTime: number) {}

	onDestroy() {
		// Tt chy nn khi scene b hy
		BackgroundRunner.disableBackgroundRunning()

		socketClient.removeAllListeners()
	}

	private initPlayerData() {
		const userInfo = JSON.parse(sys.localStorage.getItem('user-info') || 'null') as User
		this.playerA.initData(userInfo)
		this.playerA.setUserBalance({
			coin: 100,
			coinSymbol: 'tADA',
		})
		// get playerB from current users
		const currentUsers = JSON.parse(sys.localStorage.getItem('currentUsers') || '[]') as User[]
		const playerB = currentUsers.find((user) => user.walletAddress !== userInfo.walletAddress)
		if (playerB) {
			this.playerB.initData(playerB)
		} else {
			console.log('player B do not in room')
			this.playerB.initData({
				id: 0,
				alias: '---',
				walletAddress: 'addr_test1a...000',
				avatar: '',
			})
		}
		this.playerB.setUserBalance({
			coin: 0,
			coinSymbol: 'tADA',
		})
	}

	protected initChoiceBtn() {
		this.getChoiceBtn(this.rockBtnNode)?.onClick(() => {
			this.onClickChoice(Choice.Rock)
		})
		this.getChoiceBtn(this.paperBtnNode)?.onClick(() => {
			this.onClickChoice(Choice.Paper)
		})
		this.getChoiceBtn(this.scissorsBtnNode)?.onClick(() => {
			this.onClickChoice(Choice.Scissors)
		})
	}

	protected getChoiceBtn(node: Node) {
		const button = node?.getComponent(ChoiceBtnScript)
		if (!button) return null
		return button
	}

	protected setChoiceBtnInteractive([rockI, paperI, scissorsI]: boolean[]) {
		this.getChoiceBtn(this.rockBtnNode).interactable = rockI
		this.getChoiceBtn(this.paperBtnNode).interactable = paperI
		this.getChoiceBtn(this.scissorsBtnNode).interactable = scissorsI
	}
	protected setChoiceBtnSelected([rockI, paperI, scissorsI]: boolean[]) {
		this.getChoiceBtn(this.rockBtnNode).setSelected(rockI)
		this.getChoiceBtn(this.paperBtnNode).setSelected(paperI)
		this.getChoiceBtn(this.scissorsBtnNode).setSelected(scissorsI)
	}

	protected onClickPrimaryBtn(primaryBtn: PrimaryBtn): void {
		console.log('Click Ready Btn')
		if (primaryBtn.type === 'READY') {
			this.clip.onAudioQueue(3) // button click
			primaryBtn.interactable = false
			this.nav.interactable = false

			socketClient.emit('GAME', {
				action: 'READY',
				socketRoom: this.socketRoom,
				gameRoomId: this.currentRoom.id,
			})
		} else if (primaryBtn.type === 'CONFIRM') {
			primaryBtn.interactable = false
			this.clip.onAudioQueue(3) // button click

			this.playerA.setChoice(this.selectedChoice)
			socketClient.emit('GAME', {
				action: 'COMMIT',
				socketRoom: this.socketRoom,
				gameRoomId: this.currentRoom.id,
				data: {
					encryptedChoice: this.playerA.hashedChoice,
					txId: 'tx_id_commit_from_client_test_' + this.playerA.choice,
				},
			})
		}
	}

	get gameState() {
		return this._gameState
	}
	set gameState(value: GameState) {
		this._gameState = value
		this.handleGameStateChange()
	}

	protected handleGameStateChange() {
		console.log('[] Game state change', this.gameState)
		console.table([
			{
				playerA: this.playerA,
				playerB: this.playerB,
			},
		])
		switch (this.gameState) {
			case GameState.WaitingForPlayers: {
				console.log('Waiting for players', this.playerA)
				this.primaryBtn.interactable = !this.playerA.isReady
				this.nav.interactable = !this.playerA.isReady
				this.roundResultController.hide()
				break
			}
			case GameState.WaitingForChoices: {
				this.primaryBtn.interactable = !this.playerA.hasMadeChoice
				this.primaryBtn.setType('CONFIRM')
				this.setChoiceBtnInteractive(
					this.playerA.hasMadeChoice ? [false, false, false] : [true, true, true]
				)
				this.onClickChoice(this.selectedChoice)
				this.countdownController.show()
				this.countdownController.setTime(this.currentGame.roundTimeoutMs / 1000 + 1) // add 1 second for network delay
				this.countdownController.startCoundown(() => {
					// when countdown finish
					this.onCountdownFinished()
				})
				this.nav.interactable = false
				break
			}
			case GameState.CommittingChoices: {
				this.primaryBtn.setType('CONFIRM')
				this.primaryBtn.interactable = !this.playerA.hasMadeChoice
				this.setChoiceBtnInteractive(
					this.playerA.hasMadeChoice ? [false, false, false] : [true, true, true]
				)
				break
			}
			case GameState.RevealingChoices:
				this.primaryBtn.interactable = false
				this.setChoiceBtnInteractive([false, false, false])
				socketClient.emit('GAME', {
					action: 'REVEAL',
					socketRoom: this.socketRoom,
					gameRoomId: this.currentRoom.id,
					data: {
						txId: 'tx_id_reveal_from_client_test_' + this.playerA.choice,
						choice: this.playerA.choice,
						salt: this.playerA.salt,
					},
				})
				this.pushSystemNoti(
					`${this.playerA.userInfo.alias} commited [${formatString(
						this.playerA.hashedChoice,
						3,
						3
					)}]`
				)
				this.playerB.hashedChoice &&
					this.pushSystemNoti(
						`${this.playerB.userInfo.alias} commited [${formatString(
							this.playerB.hashedChoice,
							3,
							3
						)}]`
					)
				break
			case GameState.WaitingForPayout:
				this.playerA.isRevealed &&
					this.pushSystemNoti(`${this.playerA.userInfo.alias} revealed [${this.playerA.salt}]`)
				this.playerB.isRevealed &&
					this.pushSystemNoti(`${this.playerB.userInfo.alias} revealed [${this.playerB.salt}]`)
				this.pushSystemNoti('Waiting for payout...')
				break
			case GameState.ShowRoundResult: {
				this.countdownController.hide()
				this.primaryBtn.interactable = false
				this.nav.interactable = false
				this.roundResultController.hide()
				this.setChoiceBtnInteractive([false, false, false])
				this.setChoiceBtnSelected([false, false, false])

				console.log(
					`[GamePlayManager][handleGameStateChange][ShowRoundResult]`,
					this.playerA,
					this.playerB
				)
				if (this.playerA.choice !== Choice.None && this.playerB.choice !== Choice.None) {
					let result: 'WIN' | 'LOSE' | 'DRAW' = 'WIN'
					if (this.playerA.choice === this.playerB.choice) {
						result = 'DRAW'
					} else if (
						(this.playerA.choice === Choice.Rock && this.playerB.choice === Choice.Paper) ||
						(this.playerA.choice === Choice.Paper && this.playerB.choice === Choice.Scissors) ||
						(this.playerA.choice === Choice.Scissors && this.playerB.choice === Choice.Rock)
					) {
						result = 'LOSE'
					} else {
						result = 'WIN'
					}

					const amount = new CurrencyBalance(this.currentRoom.betUnit, this.currentRoom.betAmount)
					this.roundResultController.show({
						playerA: this.playerA,
						playerB: this.playerB,
						status: result,
						betAmount: Number(amount.toAda()),
						betUnitSymbol: amount.chain.symbol,
					})
				}
				break
			}
			case GameState.WaitingForNextRound:
				this.primaryBtn.setType('READY')
				this.primaryBtn.interactable = true
				this.countdownController.hide()
				this.countdownController.stopCountdown()
				this.setChoiceBtnSelected([false, false, false])
				this.setChoiceBtnInteractive([false, false, false])
				this.playerA.reset()
				this.playerB.reset()

				this.pushSystemNoti('Preparing for next round...')
				break
		}
	}

	protected onClickChoice(choice: Choice): void {
		if (!this.playerA.isReady) {
			console.log('Player A not ready')
			return
		}
		if (this.playerA.hasMadeChoice) {
			console.log('Player A has made choice')
			return
		}
		if (
			this.gameState !== GameState.WaitingForChoices &&
			this.gameState !== GameState.CommittingChoices
		) {
			console.warn('Game state not allow choice')
			return
		}
		this.selectedChoice = choice
		if (choice === Choice.None) return
		else if (choice === Choice.Rock) {
			this.setChoiceBtnSelected([true, false, false])
		} else if (choice === Choice.Paper) {
			this.setChoiceBtnSelected([false, true, false])
		} else if (choice === Choice.Scissors) {
			this.setChoiceBtnSelected([false, false, true])
		}
	}

	private readyWatcher: any
	/**
	 * @deprecated use socket event instead
	 */
	protected waitAllPlayersReady() {
		if (!this.readyWatcher) {
			this.readyWatcher = true
			this.schedule(this.checkReadyStatus, 0.1) // Kim tra mi 0.1 giy
		}
	}
	/**
	 * @deprecated use socket event instead
	 */
	protected checkReadyStatus() {
		if (this.playerA.isReady && this.playerB.isReady) {
			this.gameState = GameState.WaitingForChoices
			this.unschedule(this.checkReadyStatus)
			this.readyWatcher = null
		}
	}

	/**
	 * @deprecated use socket event instead
	 */
	protected onCountdownFinished() {
		// if (this.playerA.choice === Choice.None) {
		// 	console.log('Player A not choose')
		// 	// Randowm select
		// 	this.onClickChoice(this.playerA.makeRandomChoice())
		// }
		// // TODO: Mock data for player B, remove this later
		// if (this.playerB.choice === Choice.None) {
		// 	console.log('Player B not choose')
		// 	// Randowm select
		// 	this.playerB.setChoice(this.playerB.makeRandomChoice())
		// 	console.log('Player B choice', this.playerB)
		// }
		// this.gameState = GameState.ShowRoundResult // Use socket event instead
	}

	protected pushSystemNoti(message: string) {
		if (!this.chatManager) {
			console.log('Chat manager is not initialized')
			return
		}
		this.chatManager?.addMessage('Noti: ', message)
	}

	// Socket event handler

	private handleSocketEventGame(payload: GameResponseMessage) {
		const { action, data } = payload
		if (action === 'GET_STATE') {
			const { players } = data.game
			// Update player data:
			this.updatePlayerData(players)
		}
		console.log(`[GamePlayManager][handleSocketEventGame]: | ${action} | `, data)
	}

	private handleSocketEventRoomAction(payload: RoomActionResponse) {
		const { action, data } = payload
		if (data?.users) {
			sys.localStorage.setItem('currentUsers', JSON.stringify(data?.users || []))
		}
		this.initPlayerData()
		console.log(`[GamePlayManager][handleSocketEventRoomAction]: | ${action} | `, data)
	}

	private handleSocketEventGameStateChange(payload: GameStateChangedResponseMessage) {
		const { state, oldState, players, currentRound } = payload.data
		console.log(`[GamePlayManager][handleSocketEventGameStateChange]: | ${state} | `, payload)
		this.updatePlayerData(players)
		this.gameState = state
	}

	private handleSocketEventGameChat(payload: GameChatResponseMessage) {
		const { message, from, to, timestamp } = payload.data
		this.chatManager?.addMessage(from.alias, message)
	}

	private updatePlayerData(players: GamePlayer[]) {
		players.forEach((player) => {
			if (player.id === this.playerA.userInfo.walletAddress) {
				if (player.isReady) {
					this.playerA.ready()
				} else {
					this.playerA.reset()
					return
				}
				if (player.choice) {
					this.playerA.setChoice(player.choice)
				}
				if (player.commit) {
					const { encryptedChoice } = player.commit
					this.playerA.hashedChoice = encryptedChoice
				}
				if (player.reveal) {
					const { choice, salt } = player.reveal
					this.playerA.choice = choice
					this.playerA.salt = salt
					this.playerA.isRevealed = true
				}
			} else if (player.id === this.playerB.userInfo.walletAddress) {
				if (player.isReady) {
					this.playerB.ready()
				} else {
					this.playerB.reset()
					return
				}
				if (player.choice) {
					this.playerB.setChoice(player.choice)
				}
				if (player.commit) {
					const { encryptedChoice } = player.commit
					this.playerB.hashedChoice = encryptedChoice
				}
				if (player.reveal) {
					const { choice, salt } = player.reveal
					this.playerB.choice = choice
					this.playerB.salt = salt
					this.playerB.isRevealed = true
				}
			}
		})
	}
}
