import { _decorator, Component, Label, Node } from 'cc'
const { ccclass, property } = _decorator

@ccclass('MessageItem')
export class MessageItem extends Component {
	@property(Label)
	messageTitle: Label | null = null

	@property(Label)
	messageContent: Label | null = null

	data = {
		title: '',
		content: '',
	}

	start() {}

	update(deltaTime: number) {}

	onLoad() {
		this.messageTitle.string = this.data.title
		this.messageContent.string = this.data.content
	}

	public setMessage(title: string, content: string) {
		this.messageTitle.string = title
		this.messageContent.string = content
		this.data.title = title
		this.data.content = content
	}
}
