import {
	_decorator,
	Component,
	director,
	EditBox,
	instantiate,
	Node,
	Prefab,
	RichText,
	ScrollView,
	Size,
	sys,
	UITransform,
} from 'cc'
const { ccclass, property } = _decorator

import { GameInteraction } from '../common/web-interaction'
import { Room } from '../common/types/room.type'
import { LobbyItem } from './prefabs/LobbyItem'
import { AudioControl } from './AudioControl'
import { PlayerManager } from './PlayerManager'
import { RequestFactory } from '../common/request-factory'
import { ModalInputPassword } from './lobby/ModalInputPassword'
import { socketClient } from '../common/constants/socket-client'
import { ModalReconnect } from './ModalReconnect'

@ccclass('Lobby')
export class Lobby extends Component {
	@property({
		type: AudioControl,
		tooltip: 'add audio controller',
	})
	public clip: AudioControl

	@property(Node)
	lobbyCreateBtn: Node | null = null

	@property(Node)
	lobbyTestBtn: Node | null = null

	@property(Node)
	lobbyTabBtn: Node | null = null

	@property(ScrollView)
	lobbyScrollView: ScrollView | null = null

	@property(Prefab)
	lobbyItemPrefab: Prefab | null = null

	@property(RichText)
	loadingText: RichText | null = null

	@property({
		type: PlayerManager,
		tooltip: 'add player manager',
	})
	playerManager: PlayerManager | null = null

	@property(ModalInputPassword)
	modalInputPassword: ModalInputPassword | null = null

	@property(ModalReconnect)
	modalReconnect: ModalReconnect | null = null

	@property(EditBox)
	passwordEditbox: EditBox | null = null

	//
	lobbyItems: Room[] = []
	lobbyInteraction = new GameInteraction().lobby
	isLoading = false

	start() {
		this.lobbyCreateBtn?.on(Node.EventType.TOUCH_END, this.createLobby, this)
		this.lobbyTestBtn?.on(Node.EventType.TOUCH_END, this.onClickTestBtn, this)
		this.lobbyTabBtn?.on(Node.EventType.TOUCH_END, this.onClickLobbyTabBtn, this)
	}

	update(deltaTime: number) {}

	async onLoad() {
		this.setLoading(true)
		RequestFactory.getLobbyList()
			.then((res) => {
				console.log('fetchLobbyList', res)
				this.setLobbyItems(res.data?.items)
				this.renderLobbyList()
			})
			.catch((err) => {
				console.log('fetchLobbyList', err)
			})
			.finally(() => {
				this.setLoading(false)
			})

		// TODO: Remove this
		if (socketClient.connected) {
			console.log('socket connected')
		} else {
			socketClient.setAuth(sys.localStorage.getItem('accessToken') || '')
			socketClient.connect()
		}

		socketClient.listen('MESSAGE', (message) => {
			console.log('MESSAGE', message)
			if (message === 'ANOTHER_SESSION_LOGIN') {
				this.modalReconnect?.show()
			}
		})
		socketClient.listen('DISCONNECT', (payload) => {
			console.log('ROOM_ACTION', payload)
			this.modalReconnect?.show()
		})
	}

	protected onEnable() {
		this.fetchUserInfo()
	}

	protected fetchUserInfo() {
		// get user info from local storage
		const userInfo = JSON.parse(sys.localStorage.getItem('user-info') || 'null')
		if (userInfo && userInfo?.id) {
			this.playerManager?.setUserInfo(userInfo)
		} else {
			RequestFactory.fetchUserInfo()
				.then((res) => {
					console.log('fetchUserInfo', res)
					if (res?.data) {
						sys.localStorage.setItem('user-info', JSON.stringify(res.data))
						this.playerManager?.setUserInfo(res.data)
					}
				})
				.catch((err) => {
					console.log('fetchUserInfo', err)
					director.loadScene('start-scene')
				})
		}
	}

	protected createLobby(): void {
		console.log('Create Lobby')
	}

	protected onCickJoinLobby(roomData: Room): void {
		if (!roomData.requiredPassword) {
			this.handleJoinLobby(roomData)
				.then(() => {
					director.loadScene('game-play')
				})
				.catch((err) => {
					console.log('handleJoinLobby', err)
				})
			return
		}
		this.modalInputPassword?.show()
		this.modalInputPassword.node.on('confirm', (password: string) => {
			this.handleJoinLobby(roomData, password)
				.then(() => {
					this.modalInputPassword?.hide()
					director.loadScene('game-play')
				})
				.catch((err) => {
					if (err === 'WRONG_PASSWORD') {
						this.modalInputPassword?.setMessage('Password incorrect')
					} else if (err === 'ROOM_IS_FULL') {
						this.modalInputPassword?.setMessage('Room is full')
					} else {
						this.modalInputPassword?.setMessage(
							err?.message || err || 'Something went wrong. Please try again.'
						)
					}
				})
		})
		this.modalInputPassword.node.once('cancel', () => {
			console.log('cancel')
			this.modalInputPassword?.hide()
		})
	}

	protected async handleJoinLobby(roomData: Room, password?: string) {
		// TODO: remove this
		socketClient.emit('ROOM_ACTION', {
			roomId: roomData.id,
			action: 'LEAVE',
			socketRoom: `GAME.${roomData.code}`,
		})

		socketClient.emit('ROOM_ACTION', {
			roomId: roomData.id,
			action: 'JOIN',
			password,
		})
		return new Promise<{
			socketRoom: string
			room: Room
		}>((resolve, reject) => {
			socketClient.listen('ROOM_ACTION', (payload) => {
				if (payload.status === 'success' && payload.action === 'JOIN') {
					sys.localStorage.setItem('socket-room', payload.data.socketRoom)
					sys.localStorage.setItem('currentRoom', JSON.stringify(payload.data.room))
					sys.localStorage.setItem('currentGame', JSON.stringify(payload.data.game))
					sys.localStorage.setItem('currentUsers', JSON.stringify(payload.data.users))
					resolve(payload.data)
				}
				if (payload.status === 'error') {
					reject(payload.message)
				}
			})
		})
	}

	renderLobbyList(): void {
		console.log('[Cocos Script] Render Lobby List')
		if (!this.lobbyScrollView || !this.lobbyItemPrefab) {
			return
		}
		// Xa cc node c trong content
		const content = this.lobbyScrollView.content
		content.removeAllChildren()

		// W: 197 H: 240
		// Align to grid width 3 items in first row, 4 items in second row
		const itemNum = this.lobbyItems.length + 1
		const itemWidth = 197
		const itemHeight = 240
		const itemPerRow = 4
		const itemSpacing = 24
		for (let i = 0; i < itemNum; i++) {
			if (i == 0) {
				content.addChild(this.lobbyCreateBtn)
				this.lobbyCreateBtn.setPosition(0, 0, 0)
				continue
			}
			const lobbyItem = instantiate(this.lobbyItemPrefab)
			const row = Math.floor(i / itemPerRow)
			const col = i % itemPerRow
			lobbyItem.setPosition(
				col * itemWidth + col * itemSpacing,
				-row * itemHeight - row * itemSpacing,
				0
			)
			this.initLobbyItem(lobbyItem, this.lobbyItems[i - 1])
			content.addChild(lobbyItem)
		}

		const contentHeight =
			itemHeight * Math.ceil(itemNum / itemPerRow) + itemSpacing * Math.ceil(itemNum / itemPerRow)
		content
			.getComponent(UITransform)
			.setContentSize(new Size(content.getComponent(UITransform).width, contentHeight))
	}

	protected setLobbyItems(items: Room[]): void {
		this.lobbyItems = items
	}

	protected setVisibleLobbyList(isVisible: boolean): void {
		if (!this.lobbyScrollView) {
			return
		}
		this.lobbyScrollView.node.active = isVisible
	}

	protected initLobbyItem(node: Node, data: Room): void {
		const lobbyItemScript = node.getComponent(LobbyItem)
		if (lobbyItemScript) {
			lobbyItemScript.roomData = data
			lobbyItemScript.node.on('join-lobby', this.onCickJoinLobby, this)
		}
	}

	protected setLoading(isLoading: boolean): void {
		console.log('[Cocos Script] Set loading', isLoading)
		this.isLoading = isLoading
		if (!this.loadingText) {
			return
		}
		this.setVisibleLobbyList(!isLoading)
		this.loadingText.node.active = isLoading
	}

	protected onClickLobbyTabBtn(): void {
		this.lobbyInteraction.emitter.emit('lobby', { type: 'onClickLobbyTabBtn' })
	}

	protected onClickTestBtn(): void {
		this.lobbyInteraction.emitter.emit('lobby', { type: 'onClickTestBtn' })
	}
}
