import { _decorator, Button, Component, EditBox, instantiate, Node, Prefab, ScrollView } from 'cc'
import { MessageItem } from '../prefabs/MessageItem'
const { ccclass, property } = _decorator

@ccclass('ChatManager')
export class ChatManager extends Component {
	@property(EditBox)
	chatInput: EditBox | null = null

	@property(Button)
	chatSendButton: Button | null = null

	@property(ScrollView)
	scrollView: ScrollView | null = null

	@property(Prefab)
	messageItemPrefab: Prefab | null = null

	start() {}

	update(deltaTime: number) {}

	protected onLoad(): void {
		//
	}

	protected onEnable(): void {
		this.chatSendButton?.node?.on(Node.EventType.TOUCH_END, this.onClickSendButton, this)
		this.chatInput?.node?.on(EditBox.EventType.TEXT_CHANGED, this.onInputChat, this)

		this.scrollView.content.removeAllChildren()
	}

	protected onDisable(): void {
		this.chatSendButton?.node?.off(Node.EventType.TOUCH_END, this.onClickSendButton, this)
		this.chatInput?.node?.off(EditBox.EventType.TEXT_CHANGED, this.onInputChat, this)
	}

	protected onClickSendButton(): void {
		if (!this.chatInput?.string) return
		this.node.emit('send-message', this.chatInput.string)
		this.clearInput()
	}

	public clearInput() {
		this.chatInput.string = ''
		this.chatSendButton.interactable = false
	}

	protected onInputChat(): void {
		if (!this.chatInput?.string) {
			this.chatSendButton.interactable = false
		} else {
			this.chatSendButton.interactable = true
		}
	}

	public addMessage(title: string, content: string) {
		const messageItem = instantiate(this.messageItemPrefab)
		messageItem.getComponent(MessageItem)?.setMessage(title, content)
		this.scrollView.content.addChild(messageItem)
	}
}
